@import "https://fonts.googleapis.com/css2?family=Kumbh+Sans&display=swap";
@import "https://fonts.googleapis.com/css2?family=Kumbh+Sans:wght@700&display=swap";
:root {
  --body-bg-color: #19a2ae;
  --text-primary-title: #2d3248;
  --text-primary-subtitle: #6a6f81;
  --text-dark: #969696;
  --clr-gray-light: #ccc;
  --ff-primary: "Kumbh Sans", sans-serif;
  --fs-200: .8125rem;
  --fs-300: .875rem;
  --fs-400: 1rem;
  --fs-500: 1.125rem;
}

*, :before, :after {
  box-sizing: border-box;
}

body, h1, h2, h3, h4, p, figure, blockquote, dl, dd {
  margin: 0;
}

ul[role="list"], ol[role="list"] {
  list-style: none;
}

html:focus-within {
  scroll-behavior: smooth;
}

body {
  min-height: 100vh;
  text-rendering: optimizespeed;
  line-height: 1.6;
}

a:not([class]) {
  text-decoration-skip-ink: auto;
}

img, picture, svg {
  max-width: 100%;
  display: block;
}

input, button, textarea, select {
  font: inherit;
}

@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }

  *, :before, :after {
    scroll-behavior: auto !important;
    transition-duration: .01ms !important;
    animation-duration: .01ms !important;
    animation-iteration-count: 1 !important;
  }
}

body {
  background-color: var(--body-bg-color);
  color: var(--text-dark);
  font-family: var(--ff-primary);
  font-size: var(--fs-400);
  place-items: center;
  font-weight: 400;
  display: grid;
}

.card {
  width: 80%;
  max-width: 400px;
  background-color: #fff;
  border-radius: 1rem;
  grid-template-columns: 1fr;
  gap: 1em;
  display: grid;
  overflow: hidden;
}

.profile-container {
  height: 100%;
  grid-template-columns: 1fr;
  display: grid;
}

.profile-container > * {
  grid-area: 1 / 1 / 3 / 2;
}

.profile__cover {
  height: 50%;
  width: 100%;
  object-fit: cover;
}

.profile__details {
  place-items: center;
  margin-block-start: 3em;
  display: grid;
}

.profile__details img {
  border: 7px solid #fff;
  border-radius: 50%;
}

.profile__details .profile__name {
  color: var(--text-primary-title);
  font-weight: 700;
}

.profile__details .profile__name > span {
  color: var(--text-dark);
}

.creator-stats {
  padding-block: 1em;
  position: relative;
}

.creator-stats:before {
  content: "";
  width: 100%;
  height: 1px;
  background-color: var(--clr-gray-light);
  opacity: .75;
  display: block;
  position: absolute;
  inset: 0;
}

.creator-stats__list {
  justify-content: space-between;
  align-items: center;
  gap: 1em;
  margin: 0;
  padding: 0;
  padding-inline: 1em;
  list-style-type: none;
  display: flex;
}

.creator-stats__list > * {
  text-align: center;
  flex: 1 1 0;
}

.creator-stats__item--number {
  font-size: var(--fs-500);
  color: var(--text-primary-title);
  font-weight: 700;
}

.creator-stats__item--title {
  font-size: var(--fs-200);
  color: var(--text-primary-subtitle);
}

